//colors
$primary-color: #FF8300;

body,
html {
    font-size: 16px;
    display: flex;
}

body {
    background-color: white;
    font-family: 'Open Sans', sans-serif;
    margin: 0 auto;
}

p {
    font-style: normal;
    font-size: 1.165rem;
    line-height: 1.58;
    color: #2f2f2f;
}

button {
    width: 165px;
    color: #000;
    border-color: #FFF;
    background-color: #fff;
    border-radius: 100px;
    font-size: 13px;
    transition: background-color 0.3s;
    padding: 12px;
    display: inline-block;
    margin-bottom: 0;
    font-weight: normal;
    text-align: center;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    background-image: none;
    border: 1px solid transparent;
    white-space: nowrap;
    padding: 8px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    border-radius: 4px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 0;
    background: black;
    color: white;
    border-radius: 0;
    height: 60px;
}
